import { graphql } from 'react-apollo'
import { withRouter } from 'react-router-dom'
import gql from 'graphql-tag'
import React, { Component } from 'react'
import queryString from 'query-string'
import debounce from 'lodash/debounce'

import { mutate } from '../utils/apollo'
import Form from '../components/form/Form.js'
import H2 from '../components/headlines/H2.js'

import {
  Button,
  Col,
  FormText,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  Progress,
  Row,
} from 'reactstrap'

import StripeForm from './StripeForm.js'

const query = gql`
  query CleaningRequestFormQuery($id: String) {
    getCleaningRequestPaymentForm(id: $id)
  }
`

const mutation = gql`
  mutation CleaningRequestStep2($data: String, $submit: String) {
    createCleaningRequestPayment(data: $data, submit: $submit) {
      status
      formErrors
      form
      isPaymentSuccessful
    }
  }
`

class CleaningFormStep2 extends Component {
  constructor(props) {
    super(props)
    this.state = {
      isPosting: false,
      success: false,
      formValues: {},
      formErrors: {},
      showPayByCashConfirmation: false,
      showLoadingSpinner: false,
      showCardDeclined: false,
      showPaymentError: false,
      submitDisabled: true,
      formInitialized: false,
    }
  }

  getFormValues = parsedData => {
    let fields = parsedData.fields
    let formValues = {}
    for (const [key, value] of Object.entries(fields)) {
      if (['CheckboxInput', 'RadioSelect'].includes(value.type)) {
        formValues[key] = value.value
      } else if (value.type === 'CheckboxSelectMultiple') {
        formValues[key] = value.value ? value.value : []
      } else {
        formValues[key] = value.value ? value.value.toString() : ''
      }
    }
    return formValues
  }

  componentWillReceiveProps(nextProps) {
    if (this.state.formInitialized === false && !nextProps.data.loading) {
      const parsedData = JSON.parse(
        nextProps.data.getCleaningRequestPaymentForm
      )
      let initialState = this.getFormValues(parsedData)
      this.setState({
        formInitialized: true,
        formValues: initialState,
      })
    }
  }

  handleSubmitSuccess = res => {
    let response = res.data.createCleaningRequestPayment
    const parsedData = JSON.parse(response.form)
    let formState = this.getFormValues(parsedData)
    this.setState({
      formValues: formState,
      submitDisabled: false,
    })
    if (response.isPaymentSuccessful) {
      this.props.history.push('/home-cleaning/success/')
    }
  }

  handleSubmitFailure = res => {
    const parsedData = JSON.parse(res.data.createCleaningRequestPayment.form)
    let formState = this.getFormValues(parsedData)
    formState.stripeToken = null
    let showCardDeclined = this.state.formErrors.non_field_errors ? true : false
    this.setState({
      formValues: formState,
      submitDisabled: true,
      showCardDeclined: showCardDeclined,
    })
  }

  handleSubmitError = res => {
    let formState = this.state.formValues
    formState.stripeToken = null
    this.setState({
      formValues: formState,
      submitDisabled: false,
      showPaymentError: true,
      showLoadingSpinner: false,
    })
  }

  mutate = (submit, token) => {
    let formValues = this.state.formValues
    formValues.id = this.props.id
    if (token) {
      formValues.stripeToken = token.id
      formValues.amount = formValues.calculatedQuote
      formValues.isReactFrontendRequest = 1
      formValues.ccy = 'USD'
    }
    const queryStr = queryString.stringify(formValues)
    mutate(
      this,
      { data: queryStr, submit: submit },
      this.props.mutate,
      'createCleaningRequestPayment',
      {
        successHandler: res => this.handleSubmitSuccess(res),
        failureHandler: res => this.handleSubmitFailure(res),
        errorHandler: res => this.handleSubmitError(res),
      }
    )
  }

  handleChange = debounce(submit => {
    this.mutate(submit, null)
  }, 500)

  handleSubmit = token => {
    this.mutate('true', token)
    this.setState({
      showLoadingSpinner: true,
    })
  }

  handlePayByCashSubmit = () => {
    this.setState({
      showPayByCashConfirmation: true,
    })
  }

  handleInputChanged = (name, value) => {
    this.setState(
      state => ({
        formValues: { ...state.formValues, [name]: value },
      }),
      e => {
        this.handleChange('false')
      }
    )
  }

  payByCashToggle = () => {
    this.setState({
      showPayByCashConfirmation: false,
    })
  }

  declinedToggle = () => {
    this.setState({
      showCardDeclined: false,
    })
  }

  paymentErrorToggle = () => {
    this.setState({
      showPaymentError: false,
    })
  }

  render() {
    const { data } = this.props
    const {
      formValues,
      formErrors,
      formInitialized,
      showPayByCashConfirmation,
      showLoadingSpinner,
      showCardDeclined,
      showPaymentError,
      submitDisabled,
    } = this.state

    if (data.loading || !formInitialized) {
      return null
    }
    const parsedData = JSON.parse(data.getCleaningRequestPaymentForm)
    return (
      <div data-test="CleaningFormStep2">
        <H2 className="mt-3">Complete Your Booking</H2>
        <form ref={ref => (this.form = ref)}>
          <Form
            formValues={formValues}
            fieldsets={parsedData.fieldsets}
            fields={parsedData.fields}
            formErrors={formErrors}
            handleSubmit={this.handleSubmit}
            handleSubmitSuccess={this.handleSubmitSuccess}
            handlePayByCashSubmit={this.handlePayByCashSubmit}
            handleInputChanged={this.handleInputChanged}
            submitDisabled={showCardDeclined ? false : submitDisabled}
          />
          <div className="mt-3 mb-3">
            {formValues.city !== 'Yangon' && (
              <StripeForm
                handleToken={this.handleSubmit}
                userEmail={formValues.user_email}
                amount={formValues.calculated_quote}
                currency={formValues.currency_code}
                submitDisabled={submitDisabled}
              />
            )}
            {formValues.city === 'Yangon' && (
              <div>
                <Row>
                  <Col xl="4" lg="5" md="4" xs="12">
                    <StripeForm
                      handleToken={this.handleSubmit}
                      userEmail={formValues.user_email}
                      amount={formValues.calculated_quote}
                      currency={formValues.currency_code}
                      submitDisabled={submitDisabled}
                      isYangon={true}
                    />
                  </Col>
                  <Col xl="3" lg="3" md="2" xs="12">
                    <Button
                      disabled={submitDisabled}
                      onClick={e => this.handlePayByCashSubmit()}
                    >
                      Pay Cash
                    </Button>
                  </Col>
                </Row>
                <FormText color="muted">
                  {submitDisabled && '* Please fill out all the fields above'}
                  {!submitDisabled &&
                    '* When you submit the form, we will get back to you within 48 hours'}
                </FormText>
              </div>
            )}
          </div>
        </form>

        <Modal isOpen={showPaymentError} toggle={this.paymentErrorToggle}>
          <ModalHeader>Oops!</ModalHeader>
          <ModalBody>Something went wrong. Please try again!</ModalBody>
          <ModalFooter>
            <Button color="secondary" onClick={this.paymentErrorToggle}>
              Close
            </Button>
          </ModalFooter>
        </Modal>
        <Modal isOpen={showPayByCashConfirmation} toggle={this.payByCashToggle}>
          <ModalBody>
            Once you proceed, our team will be notified and get back to you
            within 48 hours.
          </ModalBody>
          <ModalFooter>
            <Button color="primary" onClick={() => this.handleSubmit(null)}>
              Confirm
            </Button>
            <Button color="secondary" onClick={this.payByCashToggle}>
              Close
            </Button>
          </ModalFooter>
        </Modal>
        <Modal isOpen={showLoadingSpinner}>
          <ModalHeader>Processing...</ModalHeader>
          <ModalBody>
            <Progress animated color="danger" value="100" />
          </ModalBody>
        </Modal>
        <Modal isOpen={showCardDeclined} toggle={this.declinedToggle}>
          <ModalHeader toggle={this.toggle}>Card Declined</ModalHeader>
          <ModalBody>
            Your card has been declined. Please try another card.
          </ModalBody>
          <ModalFooter>
            <Button color="secondary" onClick={this.declinedToggle}>
              Close
            </Button>
          </ModalFooter>
        </Modal>
      </div>
    )
  }
}

const queryOptions = {
  options: props => ({
    variables: {
      id: props.id,
    },
  }),
}

CleaningFormStep2 = withRouter(CleaningFormStep2)
CleaningFormStep2 = graphql(query, queryOptions)(CleaningFormStep2)
CleaningFormStep2 = graphql(mutation)(CleaningFormStep2)
export default CleaningFormStep2



