import React, { Component } from 'react'

import ContactForm from '../components/ContactForm'
import ServiceDetailViewBase from './ServiceDetailViewBase'
import Span from '../components/Span'
import withTracker from '../components/WithTracker'

class LightingView extends Component {
  render() {
    return (
      <ServiceDetailViewBase
        dataTest="LightingView"
        title={
          <Span>
            We'll <Span primary>find the light</Span> for you.
          </Span>
        }
        text="Don't do it yourself - because our electrical experts provide quality service to install all types of light fixtures. All of our electrical professionals are experienced and vetted thoroughly."
        bulletPoints={[
          'Trusted & legal professionals',
          'Backed by DDIY Promise',
          'Affordable pricing',
          'Dedicated customer service',
        ]}
      >
        <ContactForm />
      </ServiceDetailViewBase>
    )
  }
}

LightingView = withTracker(LightingView)
export default LightingView



