import React, { Component } from 'react'

import ContactForm from '../components/ContactForm'
import ServiceDetailViewBase from './ServiceDetailViewBase'
import Span from '../components/Span'
import withTracker from '../components/WithTracker'

class PlumbingView extends Component {
  render() {
    return (
      <ServiceDetailViewBase
        dataTest="PlumbingView"
        title={
          <Span>
            We'll <Span primary>fix it</Span> for you.
          </Span>
        }
        text="Don't do it yourself - because our plumbing professionals provide quality service to troubleshoot and solve your plumbing issues. All of our plumbers are experienced and vetted thoroughly."
        bulletPoints={[
          'Trusted & legal professionals',
          'Backed by DDIY Promise',
          'Affordable pricing',
          'Dedicated customer service',
        ]}
      >
        <ContactForm />
      </ServiceDetailViewBase>
    )
  }
}

PlumbingView = withTracker(PlumbingView)
export default PlumbingView



