import React, { Component } from 'react'

import ContactForm from '../components/ContactForm'
import ServiceDetailViewBase from './ServiceDetailViewBase'
import Span from '../components/Span'
import withTracker from '../components/WithTracker'

class ToiletsView extends Component {
  render() {
    return (
      <ServiceDetailViewBase
        title={
          <Span>
            We'll <Span primary>fix your washrooms</Span> for you.
          </Span>
        }
        text="Don't do it yourself - because our plumbing professionals provide quality service to solve your washroom problems. All of our plumbers are experienced and vetted thoroughly."
        bulletPoints={[
          'Trusted & legal professionals',
          'Backed by DDIY Promise',
          'Affordable pricing',
          'Dedicated customer service',
        ]}
      >
        <ContactForm />
      </ServiceDetailViewBase>
    )
  }
}

ToiletsView = withTracker(ToiletsView)
export default ToiletsView



