import { graphql } from 'react-apollo'
import { withRouter } from 'react-router-dom'
import gql from 'graphql-tag'
import React, { Component } from 'react'
import PropTypes from 'prop-types'

import {
  Col,
  Row,
  Button,
  Form,
  FormGroup,
  FormFeedback,
  Input,
  Label,
} from 'reactstrap'

import { mutate } from '../utils/apollo'
import FieldsetSSR from '../components/FieldsetSSR'
import ModalFadeIn from '../components/modals/ModalFadeIn'

const mutation = gql`
  mutation ContactForm(
    $name: String
    $email: String
    $phone: String
    $message: String
    $view: String
  ) {
    contactForm(
      name: $name
      email: $email
      phone: $phone
      message: $message
      view: $view
    ) {
      status
      formErrors
    }
  }
`

class ContactForm extends Component {
  static propTypes = {
    buttonText: PropTypes.string,
  }

  static defaultProps = {
    buttonText: 'Submit Your Request',
  }

  constructor(props) {
    super(props)
    this.initialFormValues = {
      name: '',
      email: '',
      phone: '',
      message: '',
    }
    this.state = {
      isPosting: false,
      success: false,
      formValues: this.initialFormValues,
      formErrors: {},
    }
  }

  handleChange = e => {
    const target = e.target
    this.setState(state => ({
      formValues: { ...state.formValues, [target.name]: target.value },
    }))
  }

  handleSubmitSuccess = () => {
    this.setState({ formValues: this.initialFormValues })
  }

  handleSubmit = e => {
    e.preventDefault()
    const formValues = this.state.formValues
    formValues.view = this.props.location.pathname
    mutate(this, this.state.formValues, this.props.mutate, 'contactForm', {
      successHandler: this.handleSubmitSuccess,
    })
  }

  render() {
    const { buttonText } = this.props
    const { formValues, formErrors, isPosting } = this.state
    return (
      <Form method="post" onSubmit={this.handleSubmit}>
        <FieldsetSSR isPosting={isPosting}>
          <Row>
            <Col xs="12" md="6">
              <FormGroup>
                <Label for="idName">Name</Label>
                <Input
                  id="idName"
                  name="name"
                  onChange={this.handleChange}
                  placeholder="How may we address you?"
                  type="text"
                  valid={formErrors.name === undefined ? null : false}
                  value={formValues.name}
                />
                {formErrors.name && (
                  <FormFeedback>{formErrors.name[0]}</FormFeedback>
                )}
              </FormGroup>
            </Col>
          </Row>
          <Row>
            <Col xs="12" md="6">
              <FormGroup>
                <Label for="idPhone">Phone</Label>
                <Input
                  id="idPhone"
                  name="phone"
                  onChange={this.handleChange}
                  placeholder="How can we reach you?"
                  type="text"
                  valid={formErrors.phone === undefined ? null : false}
                  value={formValues.phone}
                />
                {formErrors.phone && (
                  <FormFeedback>{formErrors.phone[0]}</FormFeedback>
                )}
              </FormGroup>
            </Col>
            <Col xs="12" md="6">
              <FormGroup>
                <Label for="idEmail">Email</Label>
                <Input
                  id="idEmail"
                  name="email"
                  onChange={this.handleChange}
                  placeholder="Your email address?"
                  type="email"
                  valid={formErrors.email === undefined ? null : false}
                  value={formValues.email}
                />
                {formErrors.email && (
                  <FormFeedback>{formErrors.email[0]}</FormFeedback>
                )}
              </FormGroup>
            </Col>
          </Row>
          <FormGroup>
            <Label for="idMessage">Message</Label>
            <Input
              type="textarea"
              name="message"
              id="idMessage"
              onChange={this.handleChange}
              placeholder="How may we help you?"
              valid={formErrors.message === undefined ? null : false}
              value={formValues.message}
            />
            {formErrors.message && (
              <FormFeedback>{formErrors.message[0]}</FormFeedback>
            )}
          </FormGroup>
          <Button color="primary">{buttonText}</Button>
          {this.state.success && (
            <div className="mt-3">
              <ModalFadeIn show={this.state.success}>
                {
                  'Thank you for reaching out! We will reply to your request as soon as possible!'
                }
              </ModalFadeIn>
            </div>
          )}
        </FieldsetSSR>
      </Form>
    )
  }
}

ContactForm = withRouter(ContactForm)
ContactForm = graphql(mutation)(ContactForm)
export default ContactForm



