/* eslint-disable */
import { ApolloClient } from 'apollo-client'
import { ApolloProvider, getDataFromTree } from 'react-apollo'
import { BatchHttpLink } from 'apollo-link-batch-http'
import { Capture } from 'react-loadable'
import { createApolloFetch } from 'apollo-fetch'
import { getBundles } from 'react-loadable/webpack'
import { Helmet } from 'react-helmet'
import { InMemoryCache } from 'apollo-cache-inmemory'
import { renderStatic } from 'glamor/server'
import { renderToString } from 'react-dom/server'
import { StaticRouter } from 'react-router-dom'
import App from './App'
import express from 'express'
import Loadable from 'react-loadable'
import React from 'react'
import setCookie from 'set-cookie-parser'
import stats from '../build/react-loadable.json'
import fs from 'fs'

const assets = require(process.env.RAZZLE_ASSETS_MANIFEST)

const server = express()
server
  .disable('x-powered-by')
  .use(express.static(process.env.RAZZLE_PUBLIC_DIR))
  .post('/*', (req, res) => {
    res.status(200).send(`
<!doctype html>
<head>
</head>
<body>
  <h1>Error Posting Data</h1>
  <p>Oh no! Something went wrong. Our engineers have been informed via email.</p>
</body>
</html>`)
  })
  .get('/*', (req, res) => {
    const apolloMiddleware = (requests, next) => {
      // Forward cookies from the original request to Django
      requests.options.headers = {}
      requests.options.headers.cookie = req.headers.cookie
      next()
    }

    let apolloRespCookies = []
    const apolloFetchAfterware = ({ response, options }, next) => {
      // If Django returns Response cookies, save them in the array above
      apolloRespCookies = setCookie.parse(
        response.headers._headers['set-cookie']
      )
      next()
    }

    const apolloFetch = createApolloFetch({
      uri: process.env.RAZZLE_SERVER_GQL_URL,
    })

    apolloFetch.batchUse(apolloMiddleware)
    apolloFetch.batchUseAfter(apolloFetchAfterware)

    const batchHttpLink = new BatchHttpLink({ fetch: apolloFetch })

    const client = new ApolloClient({
      cache: new InMemoryCache(),
      link: batchHttpLink,
      queryDeduplication: true,
      ssrMode: true,
    })

    const context = {}
    const modules = []
    const app = (
      <Capture report={moduleName => modules.push(moduleName)}>
        <ApolloProvider client={client}>
          <StaticRouter context={context} location={req.url}>
            <App />
          </StaticRouter>
        </ApolloProvider>
      </Capture>
    )

    Loadable.preloadAll().then(() => {
      getDataFromTree(app).then(() => {
        const glamorResult = renderStatic(() => renderToString(app))
        const apolloState = JSON.stringify(client.cache.extract())
        const bundles = getBundles(stats, modules)
        const chunks = bundles.filter(bundle => bundle.file.endsWith('.js'))
        const chunksString = chunks
          .map(chunk => {
            return `<script src="${process.env.PUBLIC_PATH}${
              chunk.file
            }"></script>`
          })
          .join('\n')

        if (context.url) {
          res.redirect(context.url)
        } else {
          const helmet = Helmet.renderStatic()
          let mainJs = assets.client.js
          if (process.env.NODE_ENV === 'production') {
            mainJs = `${mainJs}`
          }
          apolloRespCookies.forEach(cookie => {
            res.cookie(cookie.name, cookie.value, cookie)
          })
          res.status(200).send(
            `<!doctype html>
      <html lang="" ${helmet.htmlAttributes.toString()} style="height: 100%;">
      <head>
          <meta http-equiv="X-UA-Compatible" content="IE=edge" />
          <meta charSet='utf-8' />
          <meta name="viewport" content="width=device-width, initial-scale=1">
          <style>${glamorResult.css}</style>
          ${helmet.title.toString()}
          ${helmet.meta.toString()}
          ${helmet.link.toString()}
          ${helmet.script.toString()}
          ${
            process.env.NODE_ENV === 'production'
              ? `<script src="${assets.client.js}" defer></script>`
              : `<script src="${assets.client.js}" defer crossorigin></script>`
          }
      </head>
      <body style="height: 100%;">
        <div id="root" style="height: 100%;">${glamorResult.html}</div>
        <script>
          window.__APOLLO_STATE__ = ${apolloState};
        </script>
        <script src="${mainJs}"></script>
        ${chunksString}
        <script>
          window.main();
        </script>
      </body>
  </html>`
          )
        }
      })
    })
  })

export default server



