import { Col, Container, Row } from 'reactstrap'
import PropTypes from 'prop-types'
import React, { Component } from 'react'

import Box from '../components/Box'
import P from '../components/P'
import H1 from '../components/headlines/H1'
import Span from '../components/Span'

class ServiceDetailViewBase extends Component {
  static propTypes = {
    dataTest: PropTypes.string.isRequired,
    title: PropTypes.node.isRequired,
    text: PropTypes.string.isRequired,
    bulletPoints: PropTypes.arrayOf(PropTypes.string).isRequired,
  }

  render() {
    const { dataTest, title, text, bulletPoints } = this.props
    return (
      <Container data-test={dataTest}>
        <Row>
          <Col xs="12" sm="12" md="12" lg="5">
            <Box>
              <H1 inverted>{title}</H1>
              <P inverted className="mt-5">
                {text}
              </P>
              <ul>
                {bulletPoints.map((item, index) => (
                  <li key={`bullet_${index}`}>
                    <Span inverted>{item}</Span>
                  </li>
                ))}
              </ul>
            </Box>
          </Col>
          <Col xs="12" sm="12" md="12" lg="7">
            {this.props.children}
          </Col>
        </Row>
      </Container>
    )
  }
}

export default ServiceDetailViewBase



