import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { Container, Col, Row } from 'reactstrap'
import glamorous from 'glamorous'

import P from './P'

const ContentDiv = glamorous.div({
  backgroundColor: '#ececec',
  padding: '20px',
  fontSize: '14px',
  '@media(min-width: 768px)': {
    padding: '30px',
    fontSize: '16px',
  },
})
ContentDiv.displayName = 'ConciergeJpgItem.ContentDiv'

class ConciergeItem extends Component {
  static propTypes = {
    alt: PropTypes.string.isRequired,
    chineseTitle: PropTypes.string.isRequired,
    jpg: PropTypes.string,
    svg: PropTypes.string,
    title: PropTypes.string.isRequired,
  }

  static defaultProps = {
    jpg: undefined,
    svg: undefined,
  }

  renderJpg = () => {
    const { jpg, alt } = this.props
    return (
      <img
        src={jpg}
        alt={alt}
        style={{
          width: '100px',
          height: '100px',
          borderRadius: '25px',
        }}
      />
    )
  }

  renderSvg = () => {
    const { svg, alt } = this.props
    return (
      <div
        style={{
          display: 'inline-block',
          position: 'relative',
          width: '100px',
          height: '100px',
          background: 'lightgrey',
          borderRadius: '25px',
        }}
      >
        <img
          src={svg}
          alt={alt}
          style={{
            height: '50px',
            margin: 'auto',
            position: 'absolute',
            top: 0,
            left: 0,
            bottom: 0,
            right: 0,
          }}
        />
      </div>
    )
  }

  render() {
    const { jpg, svg, title, chineseTitle } = this.props
    return (
      <Container>
        <Row className="mt-3">
          <Col xs="12" md="8" className="mx-auto">
            <ContentDiv>
              <Row>
                <Col xs="6" sm="4" className="text-center">
                  {!!jpg && this.renderJpg()}
                  {!!svg && this.renderSvg()}
                </Col>
                <Col xs="6" sm="8" className="text-left">
                  <P>{title}</P>
                  <P>{chineseTitle}</P>
                </Col>
              </Row>
            </ContentDiv>
          </Col>
        </Row>
      </Container>
    )
  }
}

export default ConciergeItem



