import React, { Component } from 'react'

import ContactForm from '../components/ContactForm'
import ServiceDetailViewBase from './ServiceDetailViewBase'
import Span from '../components/Span'
import withTracker from '../components/WithTracker'

class ElectricalsView extends Component {
  render() {
    return (
      <ServiceDetailViewBase
        title={
          <Span>
            We'll <Span primary>fix it</Span> for you.
          </Span>
        }
        text="Don't do it yourself - because our electrical experts provide quality service to troubleshoot and solve your electrical issues. All of our electrical professionals are experienced and vetted thoroughly."
        bulletPoints={[
          'Trusted & legal professionals',
          'Backed by DDIY Promise',
          'Affordable pricing',
          'Dedicated customer service',
        ]}
      >
        <ContactForm />
      </ServiceDetailViewBase>
    )
  }
}

ElectricalsView = withTracker(ElectricalsView)
export default ElectricalsView



