import React, { Component } from 'react'
import PropTypes from 'prop-types'

class FieldsetSSR extends Component {
  static propTypes = {
    isPosting: PropTypes.bool,
  }

  static defaultProps = {
    isPosting: false,
  }

  constructor(props) {
    super(props)
    this.state = {
      didMount: false,
    }
  }

  componentDidMount() {
    this.setState({
      didMount: true,
    })
  }

  render() {
    const { didMount } = this.state
    const { children, isPosting } = this.props
    return <fieldset disabled={!didMount || isPosting}>{children}</fieldset>
  }
}

export default FieldsetSSR



