export function mutate(
  cls,
  formValues,
  mutateFunc,
  mutationName,
  {
    errorHandler = undefined,
    failureHandler = undefined,
    refetchQueries = undefined,
    setStateOnSuccess = true,
    successHandler = undefined,
  } = {}
) {
  cls.setState({ isPosting: true, success: false })
  const mutateOptions = { variables: formValues }
  if (refetchQueries) {
    mutateOptions.refetchQueries = refetchQueries
  }
  mutateFunc(mutateOptions)
    .then(res => {
      const data = res.data[mutationName]
      if (data.status === 400) {
        if (cls) {
          cls.setState({
            formErrors: JSON.parse(data.formErrors),
            isPosting: false,
            success: false,
          })
        }
      } else if (data.status === 200) {
        if (setStateOnSuccess) {
          cls.setState({ formErrors: {}, isPosting: false, success: true })
        }
        if (successHandler) {
          successHandler(res)
        }
      }
      if (data.status !== 200 && failureHandler) {
        failureHandler(res)
      }
    })
    .catch(err => {
      cls.setState({ isPosting: false, success: false })
      if (errorHandler) {
        errorHandler(err)
      }
      console.log(err)
    })
}



