import React, { Component } from 'react'
import { Col, Container, Row } from 'reactstrap'
import { graphql } from 'react-apollo'
import gql from 'graphql-tag'

const query = gql`
  {
    getHealthCheckReport
  }
`

class HealthCheckView extends Component {
  render() {
    const { data } = this.props
    if (data.loading) {
      return null
    }
    const parsedJson = JSON.parse(data.getHealthCheckReport)

    return (
      <div className="mt-3">
        {parsedJson.status === 'EVERYTHING IS GOOD' && (
          <Container>
            <Row>
              <Col>{parsedJson.status}</Col>
            </Row>
          </Container>
        )}
        {parsedJson.status === 'ERRORS FOUND!' && (
          <Container>
            <Row>
              <Col>{parsedJson.status}</Col>
            </Row>
            {Object.keys(parsedJson).map(
              key =>
                key !== 'status' && (
                  <Row key={key} className="mt-5">
                    <Col>
                      <div>
                        <strong>Country: {key}</strong>
                      </div>
                      <div>
                        Missing months:{' '}
                        {parsedJson[key].missing_months.join(',')}
                      </div>
                      <div>
                        Missing sessions:{' '}
                        {parsedJson[key].missing_sessions.join(',')}
                      </div>
                      <div>
                        Missing combinations:{' '}
                        <div className="mt-3">Month, Sessions</div>
                        {parsedJson[key].missing_combinations.map(item => (
                          <div key={item}>{item.join(',')}</div>
                        ))}
                      </div>
                    </Col>
                  </Row>
                )
            )}
          </Container>
        )}
      </div>
    )
  }
}

HealthCheckView = graphql(query)(HealthCheckView)
export default HealthCheckView



