import glamorous from 'glamorous'

const H1 = glamorous.h1(
  props => ({
    fontFamily: props.theme.FONT_SERIF,
    color: props.theme.COLOR_TEXT,
  }),
  props =>
    props.smaller && {
      fontSize: props.theme.FONT_SIZE_H1,
    },
  props =>
    props.inverted && {
      color: props.theme.COLOR_TEXT_INVERTED,
    }
)
H1.displayName = 'H1'

export default H1



