import GoogleAnalytics from 'react-ga'

export function getGA() {
  if (process.env.RAZZLE_ENVIRONMENT === 'local') {
    // during local development we never want to track anything:
    return undefined
  }

  if (!process.env.RAZZLE_GOOGLE_ANALYTICS) {
    // if no analytics code has been provided, we don't even try to initialize
    // GoogleAnalytics:
    return undefined
  }

  if (typeof window !== 'undefined') {
    // if window is defined, we are in a browser environment and we can try
    // to initialize GoogleAnalytics:
    GoogleAnalytics.initialize(process.env.RAZZLE_GOOGLE_ANALYTICS)
    return GoogleAnalytics
  }

  // We should never end up here:
  return undefined
}



