import React, { Component } from 'react'

import ContactForm from '../components/ContactForm'
import ServiceDetailViewBase from './ServiceDetailViewBase'
import Span from '../components/Span'
import withTracker from '../components/WithTracker'

class DrainageView extends Component {
  render() {
    return (
      <ServiceDetailViewBase
        title={
          <Span>
            We'll <Span primary>unclog your drains</Span> for you.
          </Span>
        }
        text="Don't do it yourself - because our plubming professionals provide quality service to remove blockages and solve your drainage issues with your sinks and showers. All of our plumbers are experienced and vetted thoroughly."
        bulletPoints={[
          'Trusted & legal professionals',
          'Backed by DDIY Promise',
          'Affordable pricing',
          'Dedicated customer service',
        ]}
      >
        <ContactForm />
      </ServiceDetailViewBase>
    )
  }
}

DrainageView = withTracker(DrainageView)
export default DrainageView



