import React, { Component } from 'react'
import { Card, CardBody } from 'reactstrap'

class Box extends Component {
  render() {
    return (
      <Card body inverse style={{ backgroundColor: '#2f2e2e' }}>
        <CardBody>{this.props.children}</CardBody>
      </Card>
    )
  }
}

export default Box



