import React, { Component } from 'react'
import PropTypes from 'prop-types'

import FormsInputCheckbox from './FormsInputCheckbox.js'
import FormsInputCheckboxMultiple from './FormsInputCheckboxMultiple.js'
// import FormsInputFileUpload from './FormsInputFileUpload.js'
import FormsInputRadio from './FormsInputRadio.js'
import FormsInputSelect from './FormsInputSelect.js'
import FormsInputText from './FormsInputText.js'
import FormsInputTextArea from './FormsInputTextArea.js'

class Form extends Component {
  static propTypes = {
    disabled: PropTypes.bool,
  }

  static defaultProps = {
    disabled: false,
  }

  handleCheckboxMultipleChange = (name, value, checked) => {
    this.props.handleCheckboxMultipleChange(name, value, checked)
  }

  handleInputChanged = (name, value) => {
    this.props.handleInputChanged(name, value)
  }

  renderText(fieldName, inputField, formErrors, formValues) {
    let value = formValues[fieldName]

    return (
      <FormsInputText
        key={fieldName}
        errors={formErrors[fieldName]}
        label={inputField.label}
        placeholder={inputField.placeholder}
        name={fieldName}
        onChange={val => this.handleInputChanged(fieldName, val)}
        value={value}
        disabled={inputField.disabled}
      />
    )
  }

  renderTextArea(fieldName, inputField, formErrors, formValues) {
    let value = formValues[fieldName]
    return (
      <FormsInputTextArea
        key={fieldName}
        label={inputField.label}
        placeholder={inputField.placeholder}
        name={fieldName}
        value={value}
        errors={formErrors[fieldName]}
        onChange={val => this.handleInputChanged(fieldName, val)}
        disabled={inputField.disabled}
      />
    )
  }

  renderSelect(fieldName, inputField, formErrors, formValues) {
    let value = formValues[fieldName]
    return (
      <FormsInputSelect
        key={fieldName}
        choices={inputField.choices}
        errors={formErrors[fieldName]}
        label={inputField.label}
        name={fieldName}
        onChange={val => this.handleInputChanged(fieldName, val)}
        value={value}
        disabled={inputField.disabled}
      />
    )
  }

  renderCheckbox(fieldName, inputField, formErrors, formValues) {
    let value = `${formValues[fieldName]}`
    return (
      <FormsInputCheckbox
        key={fieldName}
        label={inputField.label}
        name={fieldName}
        value={value}
        errors={formErrors[fieldName]}
        onChange={val => this.handleInputChanged(fieldName, val)}
        disabled={inputField.disabled}
      />
    )
  }

  renderCheckboxMultiple(fieldName, inputField, formErrors, formValues) {
    let activeChoices = formValues[fieldName]
    return (
      <FormsInputCheckboxMultiple
        key={fieldName}
        choices={inputField.choices}
        activeChoices={activeChoices}
        label={inputField.label}
        name={fieldName}
        errors={formErrors[fieldName]}
        onChange={(val, checked) =>
          this.handleCheckboxMultipleChange(fieldName, val, checked)
        }
        disabled={inputField.disabled}
      />
    )
  }

  renderRadio(fieldName, inputField, formErrors, formValues) {
    let value = formValues[fieldName]
    return (
      <FormsInputRadio
        key={fieldName}
        label={inputField.label}
        name={fieldName}
        value={value}
        errors={formErrors[fieldName]}
        choices={inputField.choices}
        onChange={val => this.handleInputChanged(fieldName, val)}
        disabled={inputField.disabled}
      />
    )
  }

  renderDateTime(fieldName, inputField, formErrors, formValues) {
    let value = formValues[fieldName]
    return (
      <FormsInputText
        key={fieldName}
        errors={formErrors[fieldName]}
        label={inputField.label}
        placeholder={inputField.placeholder}
        name={fieldName}
        onChange={val => this.handleInputChanged(fieldName, val)}
        value={value}
        disabled={inputField.disabled}
      />
    )
  }

  /*renderImage(fieldName, inputField, formErrors, formValues) {
    return (
      <FormsInputFileUpload
        key={fieldName}
        dest="marketplace_product"
        errors={formErrors[fieldName]}
        initialValue={inputField.value}
        inline={inputField.inline}
        displayValue={inputField.displayValue}
        isMandatory
        label={inputField.label}
        name={fieldName}
        onChange={val => this.handleInputChanged(fieldName, val)}
      />
    )
  }*/

  renderForm(fieldName, fields, formErrors, formValues) {
    let inputField = fields[fieldName]
    if (['NumberInput', 'TextInput', 'EmailInput'].includes(inputField.type)) {
      return this.renderText(fieldName, inputField, formErrors, formValues)
    }

    if (inputField.type === 'Textarea') {
      return this.renderTextArea(fieldName, inputField, formErrors, formValues)
    }

    if (['LazySelect', 'Select'].includes(inputField.type)) {
      return this.renderSelect(fieldName, inputField, formErrors, formValues)
    }
    if (inputField.type === 'CheckboxInput') {
      return this.renderCheckbox(fieldName, inputField, formErrors, formValues)
    }
    if (inputField.type === 'CheckboxSelectMultiple') {
      return this.renderCheckboxMultiple(
        fieldName,
        inputField,
        formErrors,
        formValues
      )
    }
    if (inputField.type === 'RadioSelect') {
      return this.renderRadio(fieldName, inputField, formErrors, formValues)
    }
    if (inputField.type === 'DateTimeInput') {
      return this.renderDateTime(fieldName, inputField, formErrors, formValues)
    }
    /*
    if (inputField.type.indexOf('CustomS3DirectWidget') >= 0) {
      return this.renderImage(fieldName, inputField, formErrors)
    }
    if (inputField.type === 'ClearableFileInput') {
      return this.renderFileField(item)
    }
    if (inputField.type === 'TagWidget') {
      return this.renderTagField(item)
    }
    */
    return null
  }

  render() {
    const { disabled, fieldsets, fields, formErrors, formValues } = this.props
    return (
      <fieldset disabled={disabled}>
        {fieldsets.map((item, index) => (
          <div key={item.title} className={index === 0 ? '' : 'mt-3'}>
            <h3>{item.title}</h3>
            {item.fields.map(fieldName =>
              this.renderForm(fieldName, fields, formErrors, formValues)
            )}
          </div>
        ))}
      </fieldset>
    )
  }
}

export default Form



