import { graphql } from 'react-apollo'
import { withRouter } from 'react-router-dom'
import gql from 'graphql-tag'
import React, { Component } from 'react'
import queryString from 'query-string'

import { mutate } from '../utils/apollo'
import Form from '../components/form/Form.js'
import H2 from '../components/headlines/H2.js'

import { Button, Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap'

const query = gql`
  query CleaningRequestFormQuery {
    getCleaningRequestForm
  }
`

const mutation = gql`
  mutation CleaningRequest($data: String) {
    createCleaningRequest(data: $data) {
      status
      formErrors
      form
      cleaningRequest {
        id
      }
    }
  }
`

class CleaningForm extends Component {
  constructor(props) {
    super(props)
    this.state = {
      isPosting: false,
      success: false,
      formValues: {},
      formErrors: {},
      showPaymentError: false,
      formInitialized: false,
    }
  }

  getFormValues = parsedData => {
    let fields = parsedData.fields
    let formValues = {}
    for (const [key, value] of Object.entries(fields)) {
      if (['CheckboxInput', 'RadioSelect'].includes(value.type)) {
        formValues[key] = value.value
      } else if (value.type === 'CheckboxSelectMultiple') {
        formValues[key] = value.value ? value.value : []
      } else {
        formValues[key] = value.value ? value.value.toString() : ''
      }
    }
    return formValues
  }

  componentWillReceiveProps(nextProps) {
    if (this.state.formInitialized === false && !nextProps.data.loading) {
      const parsedData = JSON.parse(nextProps.data.getCleaningRequestForm)
      let initialState = this.getFormValues(parsedData)
      this.setState({
        formInitialized: true,
        formValues: initialState,
      })
    }
  }

  handleSubmitSuccess = res => {
    let response = res.data.createCleaningRequest
    const parsedData = JSON.parse(response.form)
    const cleaningRequestId = response.cleaningRequest
      ? response.cleaningRequest.id
      : null
    let formState = this.getFormValues(parsedData)
    this.setState({
      formValues: formState,
    })
    if (cleaningRequestId) {
      this.props.history.push(`/home-cleaning/payment/${cleaningRequestId}/`)
    }
  }

  handleSubmitFailure = res => {
    const parsedData = JSON.parse(res.data.createCleaningRequest.form)
    let formState = this.getFormValues(parsedData)
    this.setState({
      formValues: formState,
    })
  }

  handleSubmitError = res => {
    let formState = this.state.formValues
    this.setState({
      formValues: formState,
      showPaymentError: true,
    })
  }

  handleSubmit = () => {
    let formValues = this.state.formValues
    const queryStr = queryString.stringify(formValues)
    mutate(
      this,
      { data: queryStr },
      this.props.mutate,
      'createCleaningRequest',
      {
        successHandler: res => this.handleSubmitSuccess(res),
        failureHandler: res => this.handleSubmitFailure(res),
        errorHandler: res => this.handleSubmitError(res),
      }
    )
  }

  handleCheckboxMultipleChange = (name, value, checked) => {
    let checkboxMultipleList = this.state.formValues[name]
    if (checked === true) {
      checkboxMultipleList.push(value)
    } else {
      checkboxMultipleList.pop(value)
    }
    this.setState(state => ({
      formValues: { ...state.formValues, [name]: checkboxMultipleList },
    }))
  }

  handleInputChanged = (name, value) => {
    this.setState(state => ({
      formValues: { ...state.formValues, [name]: value },
    }))
  }

  togglePaymentError = () => {
    this.setState({
      showPaymentError: false,
    })
  }

  render() {
    const { data } = this.props
    const {
      formValues,
      formErrors,
      formInitialized,
      isPosting,
      showPaymentError,
    } = this.state

    if (data.loading || !formInitialized) {
      return null
    }
    const parsedData = JSON.parse(data.getCleaningRequestForm)

    return (
      <div data-test="CleaningForm">
        <H2 className="mt-3">Describe your task</H2>
        <form ref={ref => (this.form = ref)}>
          <Form
            formValues={formValues}
            fieldsets={parsedData.fieldsets}
            fields={parsedData.fields}
            formErrors={formErrors}
            handleSubmitSuccess={this.handleNextSuccess}
            handleCheckboxMultipleChange={this.handleCheckboxMultipleChange}
            handleInputChanged={this.handleInputChanged}
            disabled={isPosting}
          />
          <Button disabled={isPosting} onClick={e => this.handleSubmit()}>
            {isPosting ? 'Loading...' : 'Get Quote'}
          </Button>
        </form>

        <Modal isOpen={showPaymentError} toggle={this.togglePaymentError}>
          <ModalHeader>Oops!</ModalHeader>
          <ModalBody>Something went wrong. Please try again!</ModalBody>
          <ModalFooter>
            <Button color="secondary" onClick={this.togglePaymentError}>
              Close
            </Button>
          </ModalFooter>
        </Modal>
      </div>
    )
  }
}

CleaningForm = withRouter(CleaningForm)
CleaningForm = graphql(query)(CleaningForm)
CleaningForm = graphql(mutation)(CleaningForm)
export default CleaningForm



