import React, { Component } from 'react'
import { Helmet } from 'react-helmet'
import { Col, Container, Row } from 'reactstrap'
import { css } from 'glamor'
import glamorous from 'glamorous'

import ContactForm from '../components/ContactForm'
import H1 from '../components/headlines/H1'
import H2 from '../components/headlines/H2'
import H3 from '../components/headlines/H3'
import P from '../components/P'
import SPAN from '../components/Span'

import ConciergeItem from '../components/ConciergeItem'

import conciergeBannerJpg from '../static/conciergeBannerJpg.jpg'
import dryCleaningJpg from '../static/dryCleaningJpg.jpg'
import groceryJpg from '../static/groceryJpg.jpg'
import packageJpg from '../static/packageJpg.jpg'
import dishwashJpg from '../static/dishwashJpg.jpg'

import membershipSvg from '../static/svg/membershipSvg.svg'
import personalSvg from '../static/svg/personalSvg.svg'
import bucketSvg from '../static/svg/bucketSvg.svg'
import laundrySvg from '../static/svg/laundrySvg.svg'
import plumbingSvg from '../static/svg/plumbingSvg.svg'
import electricalSvg from '../static/svg/electricalSvg.svg'

const boxCss = css({
  border: '1px solid black',
  padding: '15px',
  height: '200px',
  '@media(min-width: 768px)': {
    height: '280px',
  },
  '@media(min-width: 992px)': {
    height: '250px',
  },
})

const Image = glamorous.img(() => ({
  width: '100%',
}))

class ConciergeView extends Component {
  render() {
    return (
      <Container>
        <Helmet>
          <title data-test="ConciergeView">
            DDIY Concierge | ddiy-solutions.com
          </title>
        </Helmet>
        <H1>
          DDIY{' '}
          <SPAN primary textTransform="uppercase">
            Concierge
          </SPAN>
        </H1>
        <Row className="mt-3">
          <Col xs="12">
            <Image
              src={conciergeBannerJpg}
              alt="Sofa, cushions, carpet and round tables."
            />
          </Col>
        </Row>
        <Row>
          <Col className="mt-4" xs="12" md="4">
            <Col xs="12" className={`${boxCss}`}>
              <Image
                src={membershipSvg}
                alt="Membership Card"
                style={{ height: '30px' }}
              />
              <div className="mt-3 text-center">
                <P primary textTransform="uppercase">
                  Concierge Member
                </P>
              </div>
              <div className="mt-3 text-center">
                <P>
                  Our 12-month membership lets you settle house needs the way
                  you want and get the most from dedicated support from our
                  service experts.
                </P>
              </div>
            </Col>
          </Col>
          <Col className="mt-4" xs="12" md="4">
            <Col xs="12" className={`${boxCss}`}>
              <Image
                src={personalSvg}
                alt="Circle with head and shoulders inside it"
                style={{ height: '30px' }}
              />
              <div className="mt-3 text-center">
                <P primary textTransform="uppercase">
                  Personal Home Manager
                </P>
              </div>
              <div className="mt-3 mb-7 text-center">
                <P>
                  Our dedicated Home Manager will coordinate and take care of
                  your errands to make sure your home runs smoothly.
                </P>
              </div>
            </Col>
          </Col>
          <Col className="mt-4" xs="12" md="4">
            <Col xs="12" className={`${boxCss}`}>
              <div className="text-center" style={{ fontSize: '30px' }}>
                
              </div>
              <div className="text-center">
                <P primary textTransform="uppercase">
                  Start a Whole New Way of Life Now
                </P>
              </div>
              <div className="mt-3 text-center">
                <P>
                  At an additional US$50 to any existing 12-month home-cleaning
                  plan.
                </P>
              </div>
            </Col>
          </Col>
        </Row>
        <Row>
          <Col xs="12" md="8" className="mt-3 mb-3 mx-auto">
            <H2 center>Register Interest</H2>
            <ContactForm buttonText="Register Interest" />
          </Col>
        </Row>
        <Row>
          <Col className="mt-3 text-center">
            <H3>Services Included In Our Concierge Package</H3>
          </Col>
        </Row>
        <ConciergeItem
          svg={bucketSvg}
          alt="Bucket"
          title="Regular weekly cleaning service at your selected frequency"
          chineseTitle=""
        />
        <ConciergeItem
          jpg={dryCleaningJpg}
          alt="Hangar with white shirts"
          title="Dry Cleaning Pickup"
          chineseTitle=""
        />
        <ConciergeItem
          svg={laundrySvg}
          alt="Water at 30 degree celsius in a container"
          title="Regular weekly laundry service at your selected frequency"
          chineseTitle=""
        />
        <ConciergeItem
          jpg={groceryJpg}
          alt="Shelf with bottles and egg cartons"
          title="Grocery shopping"
          chineseTitle=""
        />
        <ConciergeItem
          svg={plumbingSvg}
          alt="Water drop falling from a tap"
          title="Coordination of plumbing troubleshooting and repair works"
          chineseTitle=""
        />
        <ConciergeItem
          jpg={packageJpg}
          alt="Box tied up with thread"
          title="Package pickup"
          chineseTitle=""
        />
        <ConciergeItem
          svg={electricalSvg}
          alt="Plug"
          title="Coordination of electrical troubleshooting and repair works"
          chineseTitle=""
        />
        <ConciergeItem
          jpg={dishwashJpg}
          alt="Multiple trays with numerous cutlery"
          title="Regular dishwashing service"
          chineseTitle=""
        />
        <Row>
          <Col xs="12" md="8" className="mt-3 mb-3 mx-auto">
            <H2 center>Register Interest</H2>
            <ContactForm buttonText="Register Interest" />
          </Col>
        </Row>
      </Container>
    )
  }
}

export default ConciergeView



