import React, { Component } from 'react'
import glamorous from 'glamorous'
import PropTypes from 'prop-types'

import FormsFormError from './FormsFormError.js'

const Container = glamorous.div({
  display: 'flex',
  flex: '0 0 auto',
  position: 'relative',
})

const Checkbox = glamorous.div(
  props => ({
    display: 'inline-block',
    flex: '0 0 auto',
    backgroundColor: 'inherit',
    backgroundImage: 'none',
    backgroundSize: 'contain',
    borderColor: props.theme.colorPrimary,
    borderRadius: 'none',
    borderStyle: 'solid',
    borderWidth: '1px',
    height: '20px',
    marginRight: '0.5em',
    marginTop: '0',
    width: '20px',
  }),
  props =>
    props.isHidden && {
      display: 'none',
    },
  props =>
    props.isCircle && {
      borderRadius: '20px',
      marginTop: '0.2em',
    },
  props =>
    props.isChecked && {
      backgroundColor: 'grey',
    },
  props => {
    if (props.isChecked && props.usesBackgroundImage) {
      return {
        backgroundImage: `url(${props.activeColorOverride})`,
      }
    }
  }
)

Checkbox.displayName = 'core.forms.Checkbox'

const InputCheckboxOrRadioLabel = glamorous.label(
  props => ({
    display: 'inline-block',
    color: 'black',
    fontFamily: props.theme.fontSansSerif,
    fontSize: props.theme.fontSizeInput,
  }),
  props =>
    props.isHidden && {
      display: 'none',
    },
  props =>
    props.disabled && {
      color: props.theme.colorGray,
    }
)

InputCheckboxOrRadioLabel.displayName = 'core.forms.InputCheckboxOrRadioLabel'

class FormsInputCheckbox extends Component {
  static displayName = 'core.forms.FormsInputCheckbox'

  static propTypes = {
    checked: PropTypes.bool,
    className: PropTypes.string,
    disabled: PropTypes.bool,
    errors: PropTypes.arrayOf(PropTypes.string),
    isFocused: PropTypes.bool,
    isHidden: PropTypes.bool,
    label: PropTypes.string,
    onChange: PropTypes.func.isRequired,
    value: PropTypes.string.isRequired,
  }

  static defaultProps = {
    checked: false,
    className: undefined,
    disabled: undefined,
    errors: undefined,
    isFocused: undefined,
    isHidden: undefined,
    label: undefined,
  }

  getValue = () => {
    if (this.props.value === undefined) {
      console.error(`InputCheckbox ${this.props.name} has no value!`)
      return ''
    }
    return this.props.value
  }

  handleChange = e => {
    this.props.onChange(e.target.checked)
  }

  render() {
    const {
      checked,
      className,
      disabled,
      errors,
      label,
      name,
      value,
    } = this.props
    return (
      <div className={className ? className : 'mt-3'}>
        <Container>
          <Checkbox isChecked={checked} disabled={disabled} />
          <InputCheckboxOrRadioLabel htmlFor={`id_${name}`} disabled={disabled}>
            {label}
          </InputCheckboxOrRadioLabel>
          <input
            disabled={disabled}
            id={`id_${name}`}
            onChange={this.handleChange}
            checked={checked}
            type="checkbox"
            value={value}
            style={{
              cursor: disabled ? 'not-allowed' : 'pointer',
              height: '100%',
              left: 0,
              opacity: 0,
              position: 'absolute',
              top: 0,
              width: '100%',
              zIndex: 1,
            }}
          />
          {errors && (
            <FormsFormError id={`id_${name}_error`}>{errors[0]}</FormsFormError>
          )}
        </Container>
      </div>
    )
  }
}

export default FormsInputCheckbox



