import React, { Component } from 'react'

import ContactForm from '../components/ContactForm'
import ServiceDetailViewBase from './ServiceDetailViewBase'
import Span from '../components/Span'
import withTracker from '../components/WithTracker'

class RentedSpaceCleaningView extends Component {
  render() {
    return (
      <ServiceDetailViewBase
        title={
          <Span>
            We'll <Span primary>clean your rented space</Span> for you.
          </Span>
        }
        text="Don't do it yourself - because our cleaning partners provide quality service to clean thoroughly so that you get your full deposit back. All of our cleaning professionals are experienced and vetted thoroughly."
        bulletPoints={[
          'Trusted & legal cleaners',
          'Backed by DDIY Promise',
          'Affordable pricing',
          'Dedicated customer service',
        ]}
      >
        <ContactForm />
      </ServiceDetailViewBase>
    )
  }
}

RentedSpaceCleaningView = withTracker(RentedSpaceCleaningView)
export default RentedSpaceCleaningView



