import glamorous from 'glamorous'

const SPAN = glamorous.span(
  props => ({
    textTransform: props.textTransform,
  }),
  props =>
    props.primary && {
      color: props.theme.COLOR_PRIMARY,
    },
  props =>
    props.inverted && {
      color: props.theme.COLOR_TEXT_INVERTED,
    }
)
SPAN.displayName = 'SPAN'

export default SPAN



