import React, { Component } from 'react'

import ContactForm from '../components/ContactForm'
import ServiceDetailViewBase from './ServiceDetailViewBase'
import Span from '../components/Span'
import withTracker from '../components/WithTracker'

class SocketsView extends Component {
  render() {
    return (
      <ServiceDetailViewBase
        dataTest="SocketsView"
        title={
          <Span>
            We'll <Span primary>fix your sockets</Span> for you.
          </Span>
        }
        text="Don't do it yourself - because our electrical experts provide quality service to install or repair your sockets and switches. All of our electrical professionals are experienced and vetted thoroughly."
        bulletPoints={[
          'Trusted & legal professionals',
          'Backed by DDIY Promise',
          'Affordable pricing',
          'Dedicated customer service',
        ]}
      >
        <ContactForm />
      </ServiceDetailViewBase>
    )
  }
}

SocketsView = withTracker(SocketsView)
export default SocketsView



