import React, { Component } from 'react'
import glamorous from 'glamorous'
import PropTypes from 'prop-types'

import FormsFormError from './FormsFormError.js'

const Radio = glamorous.div(
  props => ({
    display: 'inline-block',
    flex: '0 0 auto',
    backgroundColor: 'inherit',
    backgroundImage: 'none',
    backgroundSize: 'contain',
    borderColor: props.theme.colorPrimary,
    borderRadius: 'none',
    borderStyle: 'solid',
    borderWidth: '1px',
    height: '20px',
    marginRight: '0.5em',
    marginTop: '0',
    width: '20px',
  }),
  props =>
    props.isHidden && {
      display: 'none',
    },
  props =>
    props.isCircle && {
      borderRadius: '20px',
      marginTop: '0.2em',
    },
  props =>
    props.isChecked && {
      backgroundColor: props.theme.colorPrimary,
    },
  props => {
    if (props.isChecked && props.usesBackgroundImage) {
      return {
        backgroundImage: `url(${props.activeColorOverride})`,
      }
    }
  }
)

Radio.displayName = 'core.forms.Radio'

const InputCheckboxOrRadioLabel = glamorous.label(
  props => ({
    display: 'inline-block',
    color: 'black',
    fontFamily: props.theme.fontSansSerif,
    fontSize: props.theme.fontSizeInput,
  }),
  props =>
    props.isHidden && {
      display: 'none',
    },
  props =>
    props.disabled && {
      color: props.theme.colorGray,
    }
)

InputCheckboxOrRadioLabel.displayName = 'core.forms.InputCheckboxOrRadioLabel'

class FormsInputRadio extends Component {
  static displayName = 'core.forms.FormsInputRadio'

  static propTypes = {
    isHidden: PropTypes.bool,
    isFocused: PropTypes.bool,
    disabled: PropTypes.bool,
    choices: PropTypes.arrayOf(PropTypes.arrayOf(PropTypes.string)),
    errors: PropTypes.arrayOf(PropTypes.string),
    label: PropTypes.string,
    onChange: PropTypes.func.isRequired,
    value: PropTypes.bool.isRequired,
  }

  static defaultProps = {
    isHidden: undefined,
    isFocused: undefined,
    disabled: undefined,
    errors: undefined,
    label: undefined,
  }

  getValue = () => {
    if (this.props.value === undefined) {
      console.error(`InputRadio ${this.props.name} has no value!`)
      return ''
    }
    return this.props.value
  }

  handleChange = e => {
    let value = false
    if (e.target.value === 'true') {
      value = true
    }
    this.props.onChange(value)
  }

  render() {
    const { disabled, choices, name, value, label, errors } = this.props
    return (
      <div>
        {label}
        {choices.map(item => (
          <div key={`${item[1]}`}>
            <input
              name={name}
              disabled={disabled}
              id={`id_${item[1]}`}
              onChange={e => this.handleChange(e)}
              type="radio"
              value={item[0]}
              checked={item[0] === value}
            />
            {item[1]}
          </div>
        ))}

        {errors && (
          <FormsFormError id={`id_${name}_error`}>{errors[0]}</FormsFormError>
        )}
      </div>
    )
  }
}

export default FormsInputRadio



