import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { Button, FormText } from 'reactstrap'

import StripeCheckout from 'react-stripe-checkout'

class StripeForm extends Component {
  static propTypes = {
    userEmail: PropTypes.string.isRequired,
    amount: PropTypes.string,
    currency: PropTypes.string,
    submitDisabled: PropTypes.bool,
    isYangon: PropTypes.bool,
  }

  static defaultProps = {
    submitDisabled: true,
    amount: null,
    currency: 'USD',
    isYangon: false,
  }

  handleToken = token => {
    this.props.handleToken(token)
  }

  render() {
    const { userEmail, amount, currency, submitDisabled, isYangon } = this.props
    let actualAmount = parseInt(amount * 100, 10)
    return (
      <div>
        <StripeCheckout
          email={userEmail}
          token={this.handleToken}
          stripeKey={process.env.RAZZLE_STRIPE_KEY}
          amount={actualAmount}
          name="DDIY Solutions"
          description="Credit Card Payment to DDIY Solutions"
          currency={currency}
          locale="auto"
        >
          <Button disabled={submitDisabled} onClick={e => e.preventDefault()}>
            Pay Now with Credit Card
          </Button>
        </StripeCheckout>
        <FormText color="muted">
          {!isYangon &&
            submitDisabled &&
            '* Please fill out all the fields above'}
          {!isYangon &&
            !submitDisabled &&
            '* When you submit the form, we will get back to you within 48 hours'}
        </FormText>
      </div>
    )
  }
}

export default StripeForm



