import glamorous from 'glamorous'

const H3 = glamorous.h3(
  props => ({
    fontFamily: props.theme.FONT_SERIF,
    color: props.theme.COLOR_TEXT
  }),
  props =>
    props.sansSerif && {
      fontFamily: props.theme.FONT_SANS_SERIF
    },
  props =>
    props.small && {
      fontSize: props.theme.FONT_SIZE_H3_SMALL
    },
  props =>
    props.italic && {
      fontStyle: 'italic'
    }
)
H3.displayName = 'H3'

export default H3



