import React, { Component } from 'react'
import PropTypes from 'prop-types'

import { FormGroup, FormFeedback, Input, Label } from 'reactstrap'

Input.displayName = 'core.forms.Input'

class FormsInputText extends Component {
  static displayName = 'core.forms.FormsInputText'

  static propTypes = {
    backgroundcolor: PropTypes.string,
    disabled: PropTypes.bool,
    errors: PropTypes.arrayOf(PropTypes.string),
    label: PropTypes.string,
    placeholder: PropTypes.string,
    onChange: PropTypes.func.isRequired,
    value: PropTypes.string.isRequired,
  }

  static defaultProps = {
    backgroundcolor: undefined,
    disabled: undefined,
    errors: undefined,
    label: undefined,
    placeholder: undefined,
  }

  getValue = () => {
    if (this.props.value === undefined) {
      console.error(`InputText ${this.props.name} has no value!`)
      return ''
    }
    return this.props.value
  }

  handleChange = e => {
    this.props.onChange(e.target.value)
  }

  render() {
    const {
      value,
      onChange,
      errors,
      label,
      placeholder,
      name,
      ...inputProps
    } = this.props
    const displayValue = this.getValue()

    return (
      <div className="mt-3">
        <FormGroup>
          {label && <Label for={`id_${name}`}>{label}</Label>}
          <Input
            id={`id_${name}`}
            onChange={this.handleChange}
            value={displayValue}
            placeholder={placeholder}
            valid={errors ? false : null}
            {...inputProps}
          />
          {errors && (
            <FormFeedback id={`id_${name}_error`}>{errors[0]}</FormFeedback>
          )}
        </FormGroup>
      </div>
    )
  }
}

export default FormsInputText



