import { Helmet } from 'react-helmet'
import { Container, Col, Row } from 'reactstrap'
import React, { Component } from 'react'

import P from '../components/P'
import H1 from '../components/headlines/H1'
import withTracker from '../components/WithTracker'

class AboutView extends Component {
  render() {
    return (
      <Container>
        <Helmet>
          <title data-test="title-about">About</title>
        </Helmet>
        <Row>
          <Col md="2" />
          <Col xs="12" md="3">
            <H1>About</H1>
          </Col>
          <Col xs="12" md="6">
            <P>
              DDIY is a boutique home concierge platform that combines
              technology with top-rated home assistants to manage a suite of
              in-home services.
            </P>

            <P>
              From home-cleaning to troubleshooting to house party planning,
              DDIY completes your to-do lists so that you can spend more time
              living life.
            </P>

            <P>
              Since DDIYs inception, we have maintained a meticulous form of
              dedication in building a community of service professionals to
              provide quality home services. The foundation of DDIY is
              innovation and sincerity, focusing on restoring and inspiring home
              spaces all over Southeast Asia.
            </P>
          </Col>
        </Row>
        <Row>
          <Col md="2" />
          <Col xs="12" md="3">
            <H1>Our Team</H1>
          </Col>
          <Col xs="12" md="6">
            <P>Jeanette Wu, Chief Executive Officer</P>
            <P>Yue Jun, Chief Creative Officer</P>
            <P>Hilary Tseng, Chief Social Officer</P>
          </Col>
        </Row>
      </Container>
    )
  }
}

AboutView = withTracker(AboutView)
export default AboutView



