import React, { Component } from 'react'

import CleaningFormStep2 from '../components/CleaningFormStep2'
import ServiceDetailViewBase from './ServiceDetailViewBase'
import Span from '../components/Span'
import withTracker from '../components/WithTracker'

class HomeCleaningViewStep2 extends Component {
  render() {
    return (
      <ServiceDetailViewBase
        dataTest="HomeCleaningViewStep2"
        title={
          <Span>
            We'll <Span primary>clean your home</Span> for you.
          </Span>
        }
        text="Don't do it yourself - because our cleaning partners provide quality service to clean your home. All of our cleaning professionals are experienced and vetted thoroughly."
        bulletPoints={[
          'Trusted & legal cleaners',
          'Backed by DDIY Promise',
          'Affordable pricing',
          'Dedicated customer service',
        ]}
      >
        <CleaningFormStep2 id={this.props.match.params.id} />
      </ServiceDetailViewBase>
    )
  }
}

HomeCleaningViewStep2 = withTracker(HomeCleaningViewStep2)
export default HomeCleaningViewStep2



