import { Helmet } from 'react-helmet'
import { Loading } from './utils/loadable.js'
import { ThemeProvider } from 'glamorous'
import Loadable from 'react-loadable'
import React from 'react'
import Route from 'react-router-dom/Route'
import Switch from 'react-router-dom/Switch'

import Footer from './components/Footer'
import MainNavbar from './components/MainNavbar'
import ScrollToTop from './components/ScrollToTop'
import theme from './theme'

const AboutView = Loadable({
  loader: () => import('./views/AboutView'),
  loading: Loading,
})

const AirConditioningView = Loadable({
  loader: () => import('./views/AirConditioningView'),
  loading: Loading,
})

const ConciergeView = Loadable({
  loader: () => import('./views/ConciergeView'),
  loading: Loading,
})

const DrainageView = Loadable({
  loader: () => import('./views/DrainageView'),
  loading: Loading,
})

const ElectricalsView = Loadable({
  loader: () => import('./views/ElectricalsView'),
  loading: Loading,
})

const FaucetsView = Loadable({
  loader: () => import('./views/FaucetsView'),
  loading: Loading,
})

const HealthCheckView = Loadable({
  loader: () => import('./views/HealthCheckView'),
  loading: Loading,
})

const HomeView = Loadable({
  loader: () => import('./views/HomeView'),
  loading: Loading,
})

const HomeCleaningView = Loadable({
  loader: () => import('./views/HomeCleaningView'),
  loading: Loading,
})

const HomeCleaningViewStep2 = Loadable({
  loader: () => import('./views/HomeCleaningViewStep2'),
  loading: Loading,
})

const LightingView = Loadable({
  loader: () => import('./views/LightingView'),
  loading: Loading,
})

const OfficeCleaningView = Loadable({
  loader: () => import('./views/OfficeCleaningView'),
  loading: Loading,
})

const PlumbingView = Loadable({
  loader: () => import('./views/PlumbingView'),
  loading: Loading,
})

const RentedSpaceCleaningView = Loadable({
  loader: () => import('./views/RentedSpaceCleaningView'),
  loading: Loading,
})

const RequestSuccessView = Loadable({
  loader: () => import('./views/RequestSuccessView'),
  loading: Loading,
})

const SocketsView = Loadable({
  loader: () => import('./views/SocketsView'),
  loading: Loading,
})

const ToiletsView = Loadable({
  loader: () => import('./views/ToiletsView'),
  loading: Loading,
})

const App = () => (
  <ThemeProvider theme={theme}>
    <div style={{ height: '100%' }}>
      <ScrollToTop />
      <Helmet>
        <link
          rel="stylesheet"
          href="https://maxcdn.bootstrapcdn.com/bootstrap/4.0.0/css/bootstrap.min.css"
          integrity="sha384-Gn5384xqQ1aoWXA+058RXPxPg6fy4IWvTNh0E263XmFcJlSAwiGgFAW/dAiS6JXm"
          crossOrigin="anonymous"
        />
        <link
          href="https://fonts.googleapis.com/css?family=Lato:300,400,700|Lora:400,700"
          rel="stylesheet"
          type="text/css"
        />
      </Helmet>
      <div style={{ display: 'flex', flexDirection: 'column', height: '100%' }}>
        <div>
          <MainNavbar />
        </div>
        <div>
          <Switch>
            <Route exact path="/about/" component={AboutView} />
            <Route
              exact
              path="/air-conditioner/"
              component={AirConditioningView}
            />
            <Route exact path="/concierge/" component={ConciergeView} />
            <Route exact path="/" component={HomeView} />
            <Route exact path="/drainage/" component={DrainageView} />
            <Route exact path="/electricals/" component={ElectricalsView} />
            <Route exact path="/faucets/" component={FaucetsView} />
            <Route exact path="/health-check/" component={HealthCheckView} />
            <Route
              exact
              path="/home-cleaning/concierge/"
              render={props => (
                <HomeCleaningView isConcierge={true} {...props} />
              )}
            />
            <Route exact path="/home-cleaning/" component={HomeCleaningView} />
            <Route
              exact
              path="/home-cleaning/payment/:id/"
              component={HomeCleaningViewStep2}
            />
            <Route
              exact
              path="/home-cleaning/success/"
              component={RequestSuccessView}
            />
            <Route exact path="/lighting/" component={LightingView} />
            <Route
              exact
              path="/office-cleaning/"
              component={OfficeCleaningView}
            />
            <Route exact path="/plumbing/" component={PlumbingView} />
            <Route
              exact
              path="/space-cleaning/"
              component={RentedSpaceCleaningView}
            />
            <Route exact path="/sockets/" component={SocketsView} />
            <Route exact path="/toilets/" component={ToiletsView} />
          </Switch>
        </div>
        <div style={{ marginTop: 'auto' }}>
          <Footer />
        </div>
      </div>
    </div>
  </ThemeProvider>
)

export default App



