import React, { Component } from 'react'
import PropTypes from 'prop-types'
import glamorous from 'glamorous'

import FormsLabel from './FormsLabel.js'
import FormsInputCheckbox from './FormsInputCheckbox.js'
import FormsFormError from './FormsFormError.js'

const CheckboxesDiv = glamorous.div({
  maxHeight: '300px',
  overflow: 'scroll',
})
CheckboxesDiv.displayName = 'forms.FormsInputCheckboxMultiple.CheckboxesDiv'

class FormsInputCheckboxMultipleWithFilter extends Component {
  static displayName = 'forms.FormsInputCheckboxMultiple'

  static propTypes = {
    activeChoices: PropTypes.oneOfType([
      PropTypes.arrayOf(PropTypes.string),
      PropTypes.string,
    ]),
    disabled: PropTypes.bool,
    choices: PropTypes.arrayOf(PropTypes.arrayOf(PropTypes.string)),
    errors: PropTypes.arrayOf(PropTypes.string),
    label: PropTypes.string,
    onChange: PropTypes.func.isRequired,
  }

  static defaultProps = {
    activeChoices: [],
    disabled: undefined,
    choices: [],
    errors: undefined,
    label: undefined,
  }

  handleChange = (val, checked) => {
    this.props.onChange(val, checked)
  }

  render() {
    const { choices, errors, label, name } = this.props
    const activeChoices = this.props.activeChoices
    return (
      <div>
        {label && <FormsLabel htmlFor={`id_${name}`}>{label}</FormsLabel>}
        <CheckboxesDiv>
          {choices.map(item => (
            <FormsInputCheckbox
              key={item[0]}
              name={name}
              label={item[1]}
              value={item[0]}
              onChange={checked => this.handleChange(item[0], checked)}
              checked={activeChoices.includes(item[0])}
            />
          ))}
        </CheckboxesDiv>
        {errors && (
          <FormsFormError id={`id_${name}_error`}>{errors[0]}</FormsFormError>
        )}
      </div>
    )
  }
}

export default FormsInputCheckboxMultipleWithFilter



