import React, { Component } from 'react'
import PropTypes from 'prop-types'

import { FormGroup, FormFeedback, Input, Label } from 'reactstrap'

class FormsInputSelect extends Component {
  static displayName = 'core.forms.FormsInputSelect'

  static propTypes = {
    choices: PropTypes.arrayOf(PropTypes.arrayOf(PropTypes.string)),
    className: PropTypes.string,
    disabled: PropTypes.bool,
    errors: PropTypes.arrayOf(PropTypes.string),
    label: PropTypes.string,
    onChange: PropTypes.func.isRequired,
    small: PropTypes.bool,
    value: PropTypes.string.isRequired,
  }

  static defaultProps = {
    choices: [],
    className: undefined,
    disabled: undefined,
    errors: undefined,
    label: undefined,
    small: false,
  }

  getValue = () => {
    if (this.props.value === undefined) {
      console.error(`InputSelect ${this.props.name} has no value!`)
      return ''
    }
    return this.props.value
  }

  handleChange = e => {
    this.props.onChange(e.target.value)
  }

  render() {
    const {
      choices,
      className,
      errors,
      label,
      name,
      onChange,
      small,
      ...selectProps
    } = this.props
    const displayValue = this.getValue()
    return (
      <div className={className ? className : 'mt-3'}>
        <FormGroup>
          {label && <Label for={`id_${name}`}>{label}</Label>}
          <Input
            type="select"
            id={`id_${name}`}
            name={name}
            onChange={e => this.handleChange(e)}
            value={displayValue}
            valid={errors ? false : null}
            {...selectProps}
          >
            {choices.map(item => (
              <option key={item[0]} value={item[0]}>
                {item[1]}
              </option>
            ))}
          </Input>
          {errors && <FormFeedback>{errors[0]}</FormFeedback>}
        </FormGroup>
      </div>
    )
  }
}

export default FormsInputSelect



