import { Container, Row, Col } from 'reactstrap'
import { Facebook, Instagram, Mail } from 'react-feather'
import glamorous from 'glamorous'
import { css } from 'glamor'
import React from 'react'

import theme from '../theme'
import P from './P'
import H1 from './headlines/H1'
import H2 from './headlines/H2'

const FooterEl = glamorous.footer(props => ({
  backgroundColor: props.theme.COLOR_FOOTER_BG,
  color: props.theme.COLOR_TEXT_INVERTED,
  padding: '2em 0px',
}))
FooterEl.displayName = 'Footer.FooterEl'

const socialIconLink = css({
  display: 'inline-block',
  marginRight: '1em',
  color: theme.COLOR_FOOTER_TEXT_MUTED,
  ':hover': {
    color: theme.COLOR_TEXT_INVERTED,
  },
})

const socialIcon = css({
  width: '30px',
})

class Footer extends React.Component {
  render() {
    return (
      <FooterEl className="mt-3">
        <Container>
          <Row>
            <Col
              xs="12"
              sm="12"
              lg="3"
              className="d-flex flex-column justify-content-end"
            >
              <H1 inverted>
                Be In<br className="d-xs-none" /> Touch
              </H1>
            </Col>
            <Col
              xs="12"
              sm="12"
              lg="6"
              className="mt-3 mt-sm-0 d-flex flex-column justify-content-end"
            >
              <P footerMuted>
                14A Lorong Telok<br />
                Singapore 049027<br />
                &copy; 2017 DDIY Solutions Pte. Ltd. All rights reserved.
              </P>
            </Col>
            <Col
              xs="12"
              sm="12"
              lg="3"
              className="mt-3 mt-sm-0 d-flex flex-column justify-content-end"
            >
              <div style={{ paddingBottom: '10px' }}>
                <a
                  className={socialIconLink}
                  href="https://www.facebook.com/DDIY-Solutions-1517574435000239/"
                >
                  <Facebook className={socialIcon} alt="Facebook" />
                </a>
                <a
                  className={socialIconLink}
                  href="https://www.instagram.com/ddiy.studio/"
                >
                  <Instagram className={socialIcon} alt="Instagram" />
                </a>
                <a
                  className={socialIconLink}
                  href="mailto:info@ddiy-solutions.com"
                >
                  <Mail className={socialIcon} alt="Email" />
                </a>
              </div>
            </Col>
          </Row>
        </Container>
      </FooterEl>
    )
  }
}

export default Footer



