import { Link } from 'react-router-dom'
import glamorous from 'glamorous'
import { css } from 'glamor'
import PropTypes from 'prop-types'
import React from 'react'

const TitleStyles = css({
  '@media(min-width: 460px)': {
    fontSize: '16px',
  },
  '@media(min-width: 768px)': {
    fontSize: '10px',
  },
  '@media(min-width: 992px)': {
    fontSize: '16px',
  },
})

const ImageDiv = glamorous.div(
  () => ({
    position: 'relative',
    cursor: 'pointer',
    border: `2px solid white`,
    transition: 'border 0.2s ease-in-out',
  }),
  props =>
    props.isHover === true && {
      border: `2px solid ${props.theme.COLOR_PRIMARY}`,
    }
)

const Image = glamorous.img(() => ({
  width: '100%',
}))

const ImageOverlay = glamorous.div(
  props => ({
    bottom: 0,
    backgroundColor: 'rgba(255, 255, 255, 0.9)',
    left: 0,
    opacity: 0.3,
    transition: 'all 0.5s ease-in-out',
    position: 'absolute',
    right: 0,
    top: 0,
    width: '100%',
  }),
  props =>
    props.isHover === true && {
      opacity: 0.7,
    }
)

const Title = glamorous.div(
  props => ({
    position: 'absolute',
    alignItems: 'center',
    bottom: 0,
    fontFamily: props.theme.FONT_SERIF,
    color: props.theme.COLOR_TEXT,
    fontWeight: 'bold',
    fontSize: '10px',
    justifyContent: 'center',
    opacity: 100,
    left: props.textAlign ? '5%' : 0,
    right: props.textAlign ? 0 : '5%',
    textAlign: props.textAlign ? props.textAlign : 'right',
    top: '90%',
    transition: 'color 0.5s ease-in-out',
  }),
  props =>
    props.isHover === true && {
      color: props.theme.COLOR_PRIMARY,
    }
)

class MainCategoryImageLink extends React.Component {
  static propTypes = {
    src: PropTypes.string.isRequired,
    srcAlt: PropTypes.string.isRequired,
    title: PropTypes.string.isRequired,
    chineseTitle: PropTypes.string.isRequired,
    to: PropTypes.string.isRequired,
  }

  constructor(props) {
    super(props)
    this.state = {
      isHover: false,
    }
  }

  handleMouseEnter = () => {
    this.setState({ isHover: true })
  }

  handleMouseOut = () => {
    this.setState({ isHover: false })
  }

  render() {
    const { src, srcAlt, title, chineseTitle, to } = this.props
    return (
      <Link to={to}>
        <ImageDiv
          onMouseEnter={this.handleMouseEnter}
          onMouseOut={this.handleMouseOut}
          isHover={this.state.isHover}
        >
          <Image src={src} alt={srcAlt} />
          <ImageOverlay isHover={this.state.isHover} />
          <Title isHover={this.state.isHover} className={`${TitleStyles}`}>
            {title}
          </Title>
          <Title
            isHover={this.state.isHover}
            className={`${TitleStyles}`}
            textAlign={'left'}
          >
            {chineseTitle}
          </Title>
        </ImageDiv>
      </Link>
    )
  }
}

export default MainCategoryImageLink



