import React, { Component } from 'react'
import PropTypes from 'prop-types'

import { FormGroup, FormFeedback, Input, Label } from 'reactstrap'

class FormsInputTextArea extends Component {
  static displayName = 'core.forms.FormsInputTextArea'
  static propTypes = {
    disabled: PropTypes.bool,
    errors: PropTypes.arrayOf(PropTypes.string),
    label: PropTypes.string,
    placeholder: PropTypes.string,
    onChange: PropTypes.func.isRequired,
    value: PropTypes.string.isRequired,
  }

  static defaultProps = {
    disabled: undefined,
    errors: undefined,
    label: undefined,
    placeholder: undefined,
  }

  constructor(props) {
    super(props)
    this.minHeight = 120
    this.state = { height: this.minHeight }
  }

  componentDidMount() {
    this.initHeight()
  }

  componentDidUpdate() {
    this.initHeight()
  }

  initHeight() {
    if (this.state.height === this.textArea.scrollHeight) {
      return
    }
    if (this.state.height < this.textArea.scrollHeight) {
      return this.setState({ height: this.textArea.scrollHeight })
    }
  }

  getValue = () => {
    if (this.props.value === undefined) {
      console.error(`FormInputTextArea ${this.props.name} has no value!`)
      return ''
    }
    return this.props.value
  }

  handleChange = e => {
    this.props.onChange(e.target.value)
  }

  render() {
    const {
      value,
      onChange,
      errors,
      label,
      placeholder,
      name,
      ...inputProps
    } = this.props
    const displayValue = this.getValue()
    return (
      <div>
        <FormGroup>
          {label && <Label for={`id_${name}`}>{label}</Label>}
          <Input
            type="textarea"
            id={`id_${name}`}
            value={displayValue}
            placeholder={placeholder}
            onChange={this.handleChange}
            valid={errors ? false : null}
            height={this.state.height}
            innerRef={textArea => (this.textArea = textArea)}
            {...inputProps}
          />
          {errors && (
            <FormFeedback id={`id_${name}_error`}>{errors[0]}</FormFeedback>
          )}
        </FormGroup>
      </div>
    )
  }
}

export default FormsInputTextArea



