import glamorous from 'glamorous'

const ModalFadeIn = glamorous.div(
  props => ({
    alignItems: 'center',
    backgroundColor: 'green',
    color: 'white',
    display: 'block',
    fontFamily: props.theme.FONT_SERIF,
    fontWeight: '300',
    justifyContent: 'center',
    padding: '0.8em 1em',
    width: '100%',
  }),
  props => {
    if (!props.show) {
      return {
        height: '0px',
        opacity: 0,
        overflowY: 'hidden',
        padding: '0px',
        transition: 'opacity 0.2s ease',
      }
    }
    return {
      opacity: 1,
      transition: 'opacity 3s ease',
    }
  }
)
ModalFadeIn.displayName = 'ModalFadeIn'

export default ModalFadeIn



