import React, { Component } from 'react'

import ContactForm from '../components/ContactForm'
import ServiceDetailViewBase from './ServiceDetailViewBase'
import Span from '../components/Span'
import withTracker from '../components/WithTracker'

class AirConditioningView extends Component {
  render() {
    return (
      <ServiceDetailViewBase
        title={
          <Span>
            We'll <Span primary>fix your air-conditioner</Span> for you.
          </Span>
        }
        text="Don't do it yourself - because our electrical experts provide quality service to install or repair your air-conditioner. All of our electrical professionals are experienced and vetted thoroughly."
        bulletPoints={[
          'Trusted & legal professionals.',
          'Backed by DDIY Promise.',
          'Secure online payment.',
          'Dedicated customer service.',
        ]}
      >
        <ContactForm />
      </ServiceDetailViewBase>
    )
  }
}

AirConditioningView = withTracker(AirConditioningView)
export default AirConditioningView



